export interface IDistrict {
    id?: number;
    name?: string;
    cityId?: number;
}

export class District implements IDistrict {
    constructor(
        public id?: number,
        public name?: string,
        public cityId?: number,
    ) {
        this.id = id ? id : null;
        this.name = name ? name : null;
        this.cityId = cityId ? cityId : null;
    }
}
