import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { ActivatedRouteSnapshot, CanActivate, Resolve, RouterStateSnapshot, Routes } from '@angular/router';
import { JhiResolvePagingParams } from 'ng-jhipster';
import { of } from 'rxjs';
import { map } from 'rxjs/operators';
import { IUser, User } from './user.model';
import { UserService } from './user.service';
import { UserListComponent } from './user-list/user-list.component';
import { UserUpdatePopupComponent } from './user-update/user-update.component';
import { UserDeletePopupComponent } from './user-delete-dialog/user-delete-dialog.component';

@Injectable({providedIn: 'root'})
export class UserResolve implements Resolve<IUser> {
  constructor(private service: UserService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const id = route.params['login'] ? route.params['login'] : null;
    if (id) {
      return this.service.find(id).pipe(map((user: HttpResponse<User>) => user.body));
    }
    return of(new User());
  }
}

@Injectable({providedIn: 'root'})
export class UserManagementResolve implements Resolve<IUser> {
  constructor(private service: UserService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const id = route.params['login'] ? route.params['login'] : null;
    if (id) {
      return this.service.find(id).pipe(map((user: HttpResponse<User>) => user.body));
    }
    return of(new User());
  }
}

@Injectable({providedIn: 'root'})
export class UserAuthoritiesResolve implements Resolve<string[]> {
  constructor(private service: UserService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    return this.service.authorities();
  }
}

export const userRoutes: Routes = [
  {
    path: 'user',
    component: UserListComponent,
    resolve: {
      pagingParams: JhiResolvePagingParams,
    },
    data: {
      pageTitle: 'Account list',
      defaultSort: 'id,asc'
    },
    children: [
      {
        path: 'new',
        component: UserUpdatePopupComponent,
        resolve: {
          entity: UserResolve,
        },
        data: {
          authorities: ['can_user', 'can_create_user'],
          pageTitle: 'Create user'
        },
      },
      {
        path: 'edit/:login',
        component: UserUpdatePopupComponent,
        resolve: {
          entity: UserResolve,
        },
        data: {
          authorities: ['can_user', 'can_update_user'],
          pageTitle: 'Update user'
        },
      },
      {
        path: 'delete/:login',
        component: UserDeletePopupComponent,
        resolve: {
          entity: UserResolve
        },
        data: {
          authorities: ['can_user', 'can_delete_user'],
          pageTitle: 'Delete user'
        },
      },
    ],
  },
];

