import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { ActivatedRoute, Data, Router } from '@angular/router';
import { JhiEventManager } from 'ng-jhipster';
import { User } from '../user.model';
import { UserService } from '../user.service';

@Component({
  selector: 'app-user-delete-dialog',
  templateUrl: './user-delete-dialog.component.html'
})
export class UserDeleteDialogComponent {
  user: User;

  @HostListener('window:keyup.esc')
  private onKeyUp() {
    this.dialogRef.close();
  }

  constructor(
    private dialogRef: MatDialogRef<UserDeleteDialogComponent>,
    private userService: UserService,
    private eventManager: JhiEventManager
  ) {}

  confirmDelete(login) {
    this.userService.delete(login).subscribe(response => {
      this.eventManager.broadcast({
        name: 'userListModification',
        content: 'Deleted a user'
      });
      this.dialogRef.close(true);
    });
  }
}

@Component({
  selector: 'app-user-delete-popup',
  template: ''
})
export class UserDeletePopupComponent implements OnInit, OnDestroy {
  private dialogRef: MatDialogRef<UserDeleteDialogComponent>;

  constructor(private activatedRoute: ActivatedRoute, private router: Router, private dialog: MatDialog) {}

  ngOnInit() {
    this.activatedRoute.data.subscribe(({user}: Data) => {
      setTimeout(() => {
        this.dialogRef = this.dialog.open(UserDeleteDialogComponent, {
          disableClose: true,
          width: '500px',
        });

        this.dialogRef.componentInstance.user = user;

        this.dialogRef.afterClosed().subscribe(
          result => {
            this.router.navigate([{outlets: {popup: null}}], {replaceUrl: true, queryParamsHandling: 'merge'});
          },
          reason => {
            this.router.navigate([{outlets: {popup: null}}], {replaceUrl: true, queryParamsHandling: 'merge'});
          }
        );
      }, 0);
    });
  }

  ngOnDestroy() {
    this.dialogRef = null;
  }
}
