import { CriteriaSearch } from '../../../shared';

export class RoleListSearch extends CriteriaSearch {
  constructor(
    public query?: string,
  ) {
    super();
    if (!query) {
      this.query = '';
    }
  }

  valid() {
    return true;
  }

  addQueryParams() {
    this.setQueryParam('query', this.query || '');
  }
}
