import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot, Routes } from '@angular/router';
import { JhiResolvePagingParams } from 'ng-jhipster';
import { map } from 'rxjs/operators';
import { IRole, Role } from './role';
import { RoleService } from './role.service';
import { RoleListComponent } from './role-list/role-list.component';
import { RoleUpdateComponent } from './role-update/role-update.component';
import { RoleDeletePopupComponent } from './role-delete-dialog/role-delete-dialog.component';

@Injectable({providedIn: 'root'})
export class RoleResolve implements Resolve<IRole> {
  constructor(private service: RoleService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const id = route.paramMap.get('id');
    return this.service.find(id).pipe(map((role: HttpResponse<Role>) => role.body));
  }
}

export const roleRoutes: Routes = [
  {
    path: 'role',
    component: RoleListComponent,
    resolve: {
      pagingParams: JhiResolvePagingParams,
    },
    data: {
      authorities: ['user_role_index'],
      pageTitle: 'Role List',
      defaultSort: 'id,asc'
    },
  },
  {
    path: 'role/new',
    component: RoleUpdateComponent,
    resolve: {
      pagingParams: JhiResolvePagingParams,
    },
    data: {
      authorities: ['user_role_store'],
      pageTitle: 'Role Create',
      defaultSort: 'id,asc'
    },
  },
  {
    path: 'role/:id/edit',
    component: RoleUpdateComponent,
    resolve: {
      entity: RoleResolve,
    },
    data: {
      authorities: ['user_role_update'],
      pageTitle: 'Role Update'
    },
  },
];

export const rolePopupRoute: Routes = [
  {
    path: 'role/:id/delete',
    component: RoleDeletePopupComponent,
    resolve: {
      entity: RoleResolve
    },
    data: {
      authorities: ['user_role_destroy'],
      pageTitle: 'Role Delete'
    },
    outlet: 'popup'
  },
];
