export interface IWard {
    id?: number;
    name?: string;
    districtId?: number;
}

export class Ward implements IWard {
    constructor(
        public id?: number,
        public name?: string,
        public districtId?: number,
    ) {
        this.id = id ? id : null;
        this.name = name ? name : null;
        this.districtId = districtId ? districtId : null;
    }
}
