import { Moment } from 'moment';
import { Address, IAddress } from '../address';

export interface IUser {
  id?: any;
  login?: string;
  code?: string;
  firstName?: string;
  lastName?: string;
  fullName?: string;
  address?: IAddress|string;
  addressId?: number;
  addressName?: string;
  countryId?: number;
  cityId?: number;
  districtId?: number;
  wardId?: number;
  birthday?: Moment;
  phone?: string;
  email?: string;
  activated?: boolean;
  langKey?: string;
  role?: string;
  scope?: string;
  authorities?: any[];
  createdBy?: string;
  createdDate?: Date;
  lastModifiedBy?: string;
  lastModifiedDate?: Date;
  password?: string;
  userDetailId?: string;
  avatar?: string;
  roleId?: string;
  gender?: number;
}

export class User implements IUser {
  constructor(
    public id?: any,
    public login?: string,
    public code?: string,
    public firstName?: string,
    public lastName?: string,
    public fullName?: string,
    public address?: IAddress|string,
    public addressId?: number,
    public addressName?: string,
    public countryId?: number,
    public cityId?: number,
    public districtId?: number,
    public wardId?: number,
    public birthday?: Moment,
    public phone?: string,
    public email?: string,
    public activated?: boolean,
    public langKey?: string,
    public role?: string,
    public scope?: string,
    public authorities?: any[],
    public createdBy?: string,
    public createdDate?: Date,
    public lastModifiedBy?: string,
    public lastModifiedDate?: Date,
    public password?: string,
    public userDetailId?: string,
    public avatar?: string,
    public roleId?: string,
    public gender?: number,
  ) {
    this.id = id ? id : null;
    this.login = login ? login : null;
    this.code = code ? code : null;
    this.firstName = firstName ? firstName : null;
    this.lastName = lastName ? lastName : null;
    this.fullName = fullName ? fullName : null;
    this.address = address ? address : new Address();
    this.addressId = addressId ? addressId : null;
    this.addressName = addressName ? addressName : null;
    this.countryId = countryId ? countryId : null;
    this.cityId = cityId ? cityId : null;
    this.districtId = districtId ? districtId : null;
    this.wardId = wardId ? wardId : null;
    this.birthday = birthday ? birthday : null;
    this.phone = phone ? phone : null;
    this.email = email ? email : null;
    this.activated = activated ? activated : false;
    this.langKey = langKey ? langKey : null;
    this.authorities = authorities ? authorities : null;
    this.createdBy = createdBy ? createdBy : null;
    this.createdDate = createdDate ? createdDate : null;
    this.lastModifiedBy = lastModifiedBy ? lastModifiedBy : null;
    this.lastModifiedDate = lastModifiedDate ? lastModifiedDate : null;
    this.password = password ? password : null;
    this.userDetailId = userDetailId ? userDetailId : null;
    this.avatar = avatar ? avatar : null;
    this.roleId = roleId ? roleId : null;
    this.gender = gender ? gender : 1;
  }

  assign(props?: IUser) {
    if (props) {
      for (const i in props) {
        if (typeof this[i] !== 'undefined') {
          this[i] = props[i];
        }
      }
    }
    return this;
  }
}
