import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Data, Router } from '@angular/router';

import { Principal } from '../../../core';
import { JhiAlertService } from 'ng-jhipster';
import { MatDialog, MatDialogRef, MatSelect } from '@angular/material';
import { IUser, User } from '../user.model';
import { UserService } from '../user.service';
import { EMPTY, Observable } from 'rxjs';
import { IRole, RoleService } from '../../role';
import { AddressService, CityService, IAddress, ICity, ICountry, IDistrict, IWard } from '../../address';
import { map } from 'rxjs/operators';
import { CountryService } from '../../address/country.service';
import { DistrictService } from '../../address/district.service';
import { WardService } from '../../address/ward.service';
import { isObject } from 'util';
import { FilterOperator } from 'src/app/shared';

@Component({
  selector: 'app-user-update-dialog',
  templateUrl: './user-update.component.html'
})
export class UserUpdateDialogComponent implements OnInit {
  title: string;
  currentAccount: IUser;
  editMode: boolean;
  entity: IUser;
  roles$: Observable<IRole[]>;
  addresses$: Observable<IAddress[]>;
  countries$: Observable<ICountry[]>;
  cities$: Observable<ICity[]>;
  districts$: Observable<IDistrict[]>;
  wards$: Observable<IWard[]>;
  @ViewChild('allSelected') private allSelected: MatSelect;
  constructor(
    private dialogRef: MatDialogRef<UserUpdateDialogComponent>,
    private principal: Principal,
    private service: UserService,
    private roleService: RoleService,
    private addressService: AddressService,
    private countryService: CountryService,
    private cityService: CityService,
    private districtService: DistrictService,
    private wardService: WardService,
  ) {
    this.title = 'user.createLabel';
    this.entity = new User();
    this.roles$ = EMPTY;
    this.addresses$ = EMPTY;
    this.countries$ = EMPTY;
    this.cities$ = EMPTY;
    this.districts$ = EMPTY;
    this.wards$ = EMPTY;
  }

  ngOnInit() {
    this.principal.identity().then(account => {
      this.currentAccount = account;
    });

    this.roles$ = this.roleService.list().pipe(
      map((res) => res.body),
    );
    this.addresses$ = this.addressService.list().pipe(
      map((res) => res.body),
    );
    this.countries$ = this.countryService.list().pipe(
      map((res) => res.body),
    );

    if (this.entity.id !== null) {
      this.title = 'user.updateLabel';
    }
  }

  clear() {
    this.dialogRef.close('cancel');
  }

  save() {
    if (this.entity.id !== null) {
      this.service.update(this.entity).subscribe(() => this.onSaveSuccess(), () => this.onSaveError());
    } else {
      this.service.create(this.entity).subscribe(() => this.onSaveSuccess(), () => this.onSaveError());
    }
  }

  private onSaveSuccess() {
    this.dialogRef.close(true);
  }

  private onSaveError() {
  }

   /** Change option template for Selects */
   optionLabel(item) { return item.name; }

   /**
   * Get cities by country id change event
   *
   * @param {*} countryId
   * @returns
   * @memberof EquipmentFormComponent
   */
  onCountryChange(countryId) {
    this.entity.cityId = null;
    this.cities$ = null;
    this.entity.districtId = null;
    this.districts$ = null;
    this.entity.wardId = null;
    this.wards$ = null;
    if (!countryId || countryId === 'null') {
      return;
    }
    const req = {};
    req[`countryId.${FilterOperator.equals}`] = countryId;
    req['size'] = 100;
    this.cities$ = this.cityService.list(req).pipe(
      map((res) => res.body),
    );
  }

   /**
   * Get districts by city id change event
   *
   * @param {*} cityId
   * @returns
   * @memberof EquipmentFormComponent
   */
  onCityChange(cityId) {
    this.entity.districtId = null;
    this.districts$ = null;
    this.entity.wardId = null;
    this.wards$ = null;
    if (!cityId || cityId === 'null') {
      return;
    }
    const req = {};
    req[`cityId.${FilterOperator.equals}`] = cityId;
    req['size'] = 100;
    this.districts$ = this.districtService.list(req).pipe(
      map((res) => res.body),
    );
  }

  /**
   * Get wards by district id change event
   *
   * @param {*} districtId
   * @returns
   * @memberof EquipmentFormComponent
   */
  onDistrictChange(districtId) {
    this.entity.wardId = null;
    this.wards$ = null;
    if (!districtId || districtId === 'null') {
      return;
    }
    const req = {};
    req[`districtId.${FilterOperator.equals}`] = districtId;
    req['size'] = 100;
    this.wards$ = this.wardService.list(req).pipe(
      map((res) => res.body),
    );
  }
}

@Component({
  selector: 'app-user-update-popup',
  template: ''
})
export class UserUpdatePopupComponent implements OnInit, OnDestroy {
  private dialogRef: MatDialogRef<UserUpdateDialogComponent>;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private alertService: JhiAlertService,
    private dialog: MatDialog,
  ) { }

  ngOnInit() {
    this.activatedRoute.data.subscribe(({ entity }: Data) => {
      setTimeout(() => {
        this.dialogRef = this.dialog.open(UserUpdateDialogComponent, {
          disableClose: true,
          width: '500px',
        });
        this.dialogRef.componentInstance.entity = new User().assign(entity);
        this.dialogRef.afterClosed().subscribe((result) => this.previousState(result), (reason) => this.previousState(reason));
      });
    });
  }

  private onSuccess(res) {
  }

  private onError(error: any) {
    this.alertService.error(error.error, error.message, null);
  }

  previousState(result?: any) {
    const params: any = {};
    if (result && isObject(result)) {
      Object.assign(params, result);
    }
    this.router.navigate(['.'], {relativeTo: this.activatedRoute.parent, queryParams: params, queryParamsHandling: 'merge'});
    this.dialogRef = null;
  }

  ngOnDestroy(): void {
    this.dialogRef = null;
  }
}
