import {AfterViewInit, Component, OnInit} from '@angular/core';
import {HttpErrorResponse} from '@angular/common/http';
import {Title} from '@angular/platform-browser';
import {ActivatedRoute} from '@angular/router';
import {JhiAlertService} from 'ng-jhipster';
import {forkJoin, Observable} from 'rxjs';
import {Account, AccountService, IAccount, Principal} from '../../../core';
import {IPasswordChange, PasswordChange} from '../../../shared';
import {UserService} from '../../user/user.service';
import * as moment from 'moment';
import {FormBuilder, FormGroup} from '@angular/forms';

@Component({
  selector: 'app-personal-setting',
  templateUrl: './personal-setting-bootstrap.component.html',
  styleUrls: ['./personal-setting.component.scss']
})
export class PersonalSettingComponent implements OnInit, AfterViewInit {
  account: IAccount;
  account_update: any;
  passwordChange: IPasswordChange;
  isSaving: boolean;
  isSavingPass: boolean;

  public imagePath;
  envent: any;
  imgURL: any;
  public message: string;
  uploadForm: FormGroup;
  // top: TopbarComponent;

  constructor(
    private accountService: AccountService,
    private userService: UserService,
    private alertService: JhiAlertService,
    private activatedRoute: ActivatedRoute,
    private titleService: Title,
    private principal: Principal,
    private formBuilder: FormBuilder,
  ) {
    this.passwordChange = new PasswordChange();
    this.account = new Account();
    this.uploadForm = this.formBuilder.group({
      name: [''],
      avatar: [null]
    });
  }

  ngOnInit(): void {
    this.activatedRoute.data.subscribe(({account}) => {
      if (account.birthday) {
        account.birthday = account.birthday != null ? moment(account.birthday).format('DD-MM-YYYY') : null;
      }
      if (account) {
        this.account = account;
        this.imgURL = this.account.imageUrl;
      }
    });
    console.log(this.imgURL);
    if (this.imgURL == null) {
      this.imgURL = 'assets/images/img-user.png';
    }
  }

  ngAfterViewInit() {
  }
  preview(envent, files) {
    this.envent = envent;
    if (files.length === 0) {
      return;
    }
    const mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.message = 'Only images are supported.';
      return;
    }

    const reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.imgURL = reader.result;
    };
  }
  isCurrentPasswordRequired(): boolean {
    return (this.passwordChange.newPassword !== null && this.passwordChange.newPassword !== '');
  }

  isNewPasswordRequired(): boolean {
    return this.passwordChange.currentPassword !== null && this.passwordChange.currentPassword !== '';
  }

  isConfirmPasswordRequired(): boolean {
    return (this.passwordChange.currentPassword !== null && this.passwordChange.currentPassword !== '')
      || (this.passwordChange.newPassword !== null && this.passwordChange.newPassword !== '');
  }

  isConfirmPasswordMatched(): boolean {
    return (this.passwordChange.newPassword !== undefined
      && this.passwordChange.newPassword !== null
      && this.passwordChange.newPassword !== '')
      && (this.passwordChange.newPassword === this.passwordChange.confirmPassword);
  }

  private onSaveSuccess(msg: string) {
    this.alertService.success(msg, null, null);
  }

  private onSaveError(msg: string) {
    this.alertService.error(msg, null, null);
  }

  refresh(): void {
    window.location.reload();
  }

  private subscribeToSaveResponse(result: Observable<any[]>) {
    result.subscribe(([accountHttpResponse]) =>
        setTimeout(() => {
          this.isSaving = false;
          // this.onSaveSuccess('Success to save your\'re information!');
        }, 0)
      ,
      (error: HttpErrorResponse) =>
        setTimeout(() => {
          this.isSaving = false;
          // this.onSaveError(error.error.title);
        }, 0)
    );
  }
  // uploadFile(event) {
  //   const file = (event.target as HTMLInputElement).files[0];
  //   this.uploadForm.patchValue({
  //     avatar: this.imagePath
  //   });
  //   this.uploadForm.get('avatar').updateValueAndValidity()
  // }

  updload_avatar(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.uploadForm = this.formBuilder.group({
      images: file
    });
    const formData = new FormData();
    formData.append('images', this.uploadForm.get('images').value);
    this.accountService.upload(formData, this.account.id).subscribe(
      (res) => {
        this.account.imageUrl = 'http://s3.aquaman.aztrace.vn/upload/' + res;
      },
      (err) => console.log(err)
    );
  }
  saveInfo() {
    if (this.account.imageUrl !== this.imgURL && this.envent !== undefined) {
      console.log(this.envent);
      this.updload_avatar(this.envent);
    }

    this.isSaving = true;
  }

  savePassword() {

  }

  private onSavePassSuccess(response: any) {
    this.isSavingPass = false;
    this.alertService.success('Success to save your\'re information!', null, 'right');
  }

  private onSavePassError() {
    this.isSavingPass = false;
  }

  private fetchData() {
    this.ngOnInit();
  }
}
