import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { HttpResponse } from '@angular/common/http';

import { ActivatedRoute, Router } from '@angular/router';
import { JhiAlertService, JhiEventManager, JhiParseLinks } from 'ng-jhipster';

import { Principal } from '../../../core';
import { MatTableDataSource, PageEvent, MatDialog } from '@angular/material';
import { Subscription } from 'rxjs';
import { Title } from '@angular/platform-browser';
import { ITEMS_PER_PAGE, PAGE_SIZE_OPTIONS } from '../../../shared/constants/pagination.constants';
import { IUser, User } from '../user.model';
import { UserService } from '../user.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html'
})
export class UserListComponent implements OnInit {
  displayedColumns: string[] = ['status', 'id', 'name', 'email', 'phone', 'role', 'action'];
  dataSource;
  filterForm: FormGroup;
  total: number;
  pageSize = 20;
  eventSubscriber: Subscription;
  constructor(
    private readonly dialog: MatDialog,
    private readonly route: ActivatedRoute,
    private readonly router: Router,
    private readonly fb: FormBuilder,
    private eventManager: JhiEventManager,
    private readonly userService: UserService,
    private titleService: Title,
  ) {
  }

  ngOnInit() {
    this.route.data.subscribe(({pageTitle}) => {
      this.titleService.setTitle(pageTitle);
    });

    this.filterForm = this.fb.group({
      userName: null
    });
    this.filterForm.valueChanges.subscribe((formData) => {
      const userName = formData.userName;
      const newQueryParams: {userName?: string} = {};
      newQueryParams.userName = userName;
      this.router.navigate([], {
        relativeTo: this.route,
        queryParams: newQueryParams,
        queryParamsHandling: 'merge',
      });
    });
    this.loadAll();
    this.changedUser();
  }

  loadAll() {
    const page = this.route.snapshot.queryParamMap.get('page');
    const size = this.route.snapshot.queryParamMap.get('size');
    const userName = this.route.snapshot.queryParamMap.get('userName');
    const queries = {};
    if (page) {
      queries['page'] = +page + 1;
    }
    if (size) {
      queries['size'] = size;
    }
    if (userName) {
      queries['query'] = userName;
    }
    this.userService.query(queries).subscribe((users: HttpResponse<any>) => {
      this.total = users.body.total;
      this.pageSize = users.body.per_page;
      this.dataSource = users.body.data;
    });
  }

  changedUser() {
    this.eventSubscriber = this.eventManager.subscribe('userListModification',
      () => {
        this.loadAll();
      });
  }

  openEditDialog(user) {
    const queryParams = {
      id: user.id
    };
    this.router.navigate(['', { outlets: { popup: 'user/edit/' + user.id }}], {
      relativeTo: this.route, queryParams,
      queryParamsHandling: 'preserve',
    });
  }
  openDeleteDialog(user) {
    const queryParams = {
      id: user.id
    };
    this.router.navigate(['', { outlets: { popup: 'user/delete/' + user.id }}], {
      relativeTo: this.route, queryParams,
      queryParamsHandling: 'preserve',
    });
  }


  pageChanged(event) {
    const queryParams = {
      page: event.pageIndex,
      size: event.pageSize,
    };
    this.router.navigate([], {
      relativeTo: this.route, queryParams,
      queryParamsHandling: 'merge',
    });
  }

}
