import { AfterContentInit, AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material';
import { IPermission, IRole, IRoleTreeNode, Role } from '../role';
import { RoleService } from '../role.service';
import { HttpResponse } from '@angular/common/http';
import { SelectionModel } from '@angular/cdk/collections';
import { ActivatedRoute, Data, Router } from '@angular/router';

/** Flat node with expandable and level information */
interface FlatNode {
  expandable: boolean;
  name: string;
  id: string;
  level: number;
  selected: boolean;
}

@Component({
  selector: 'app-role-update',
  templateUrl: './role-update.component.html',
  styleUrls: ['./role-update.component.scss']
})
export class RoleUpdateComponent implements OnInit, AfterContentInit {
  treeControl = new FlatTreeControl<FlatNode>(
    node => node.level, node => node.expandable);
  treeFlattener;
  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
  breakpoint;
  gridHeight;
  rowSpanForm;
  rowSpanTree;
  @ViewChild('tree') tree;
  @ViewChild('matTreeContainer') matTreeContainer;
  /** The selection for checklist */
  checklistSelection = new SelectionModel<FlatNode>(true /* multiple */);

  /** ---- ROLE SERVICES ---- */
  role: IRole;

  constructor(
    private service: RoleService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
  ) {
    this.treeFlattener = new MatTreeFlattener(
      this._transformer, node => node.level, node => node.expandable, node => node.children);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    this.dataSource.data = [];
    this.role = new Role();
  }

  ngOnInit() {
    this.breakpoint = this._breakpoint(window.innerWidth);
    this.resizeGrid();

    /** ---- ROLE SERVICES ---- */
    this.activatedRoute.data.subscribe(({entity}: Data) => {
      if (entity) {
        this.role = entity;
      }
    });
    this.getTreeNodes();
  }

  private _transformer = (node: IRoleTreeNode, level: number) => {
    return {
      expandable: !!node.children && node.children.length > 0,
      name: node.name,
      level: level,
      id: node.id,
      selected: node.selected,
    };
  }

  ngAfterContentInit() {
    const _this = this;
    setTimeout(function () {
      _this.resizeGrid();
    }, 1000);
  }

  getLevel = (node: FlatNode) => node.level;

  resizeGrid() {
    if (this.breakpoint === 1) {
      this.gridHeight = `300px`;
      this.rowSpanTree = this.matTreeContainer.nativeElement.offsetHeight / 300;
      this.rowSpanForm =  1;
    } else {
      this.gridHeight = this.matTreeContainer.nativeElement.offsetHeight;
      this.rowSpanTree = 1;
      this.rowSpanForm = 1;
    }
  }

  hasChild = (_: number, node: FlatNode) => node.expandable;

  _breakpoint = (width) => {
    return (width <= 800) ? 1 : 2;
  }

  onResize(event) {
    this.breakpoint = this._breakpoint(event.target.innerWidth);
    this.resizeGrid();
  }

  onTreeClick(event) {
    this.resizeGrid();
  }

  /** Whether all the descendants of the node are selected. */
  descendantsAllSelected(node: FlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    return descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
  }

  /** Whether part of the descendants are selected */
  descendantsPartiallySelected(node: FlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  /** Toggle the to-do item selection. Select/deselect all the descendants node */
  todoItemSelectionToggle(node: FlatNode): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    this.checkAllParentsSelection(node);
  }

  /* Checks all the parents when a leaf node is selected/unselected */
  checkAllParentsSelection(node: FlatNode): void {
    let parent: FlatNode | null = this.getParentNode(node);
    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  /* Get the parent node of a node */
  getParentNode(node: FlatNode): FlatNode | null {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }

  /** Check root node checked state and change it accordingly */
  checkRootNodeSelection(node: FlatNode): void {
    const nodeSelected = this.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    if (nodeSelected && !descAllSelected) {
      this.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.checklistSelection.select(node);
    }
  }

  /** Toggle a leaf to-do item selection. Check all the parents to see if they changed */
  todoLeafItemSelectionToggle(node: FlatNode): void {
    this.checklistSelection.toggle(node);
    this.checkAllParentsSelection(node);
  }

  /** ---------- ROLE SERVICES ----------- */

  getTreeNodes() {
    this.service.tree().subscribe((response: HttpResponse<any>) => {
      const result = response.body;
      if (result) {
        const permissions = this.role.permissions;
        this.dataSource.data = result.map((item: IRoleTreeNode) => {
          return {
            name: item.description,
            children: item.children.map((itemChild: IPermission) => {
              return {
                name: itemChild.text,
                id: itemChild.id,
                selected: permissions.includes(itemChild.id),
              };
            }),
          };
        });
        this.tree.treeControl.expandAll();
        this.treeControl.dataNodes.forEach((node: FlatNode) => {
          if (node.selected) {
            this.checklistSelection.select(node);
          }
        });
      }
    });
  }

  save() {
    this.role.permissions = this.checklistSelection.selected.filter((node: FlatNode) => {
      return node.level === 1 && node.expandable === false;
    }).map(value => value.id);
    if (this.role.id !== null) {
      this.service.update(this.role).subscribe(() => this.onSaveSuccess(), () => this.onSaveError());
    } else {
      this.service.create(this.role).subscribe(() => this.onSaveSuccess(), () => this.onSaveError());
    }
  }

  private onSaveSuccess() {
    this.router.navigate(['/role'], {});
  }

  private onSaveError() {
  }
}
