export interface ICity {
    id?: number;
    name?: string;
    countryId?: number;
}

export class City implements ICity {
    constructor(
        public id?: number,
        public name?: string,
        public countryId?: number,
    ) {
        this.id = id ? id : null;
        this.name = name ? name : null;
        this.countryId = countryId ? countryId : null;
    }
}
