import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { IRole } from '../role';
import { MatDialog, MatDialogRef } from '@angular/material';
import { RoleService } from '../role.service';
import { JhiEventManager } from 'ng-jhipster';
import { ActivatedRoute, Data, Router } from '@angular/router';

@Component({
  selector: 'app-role-delete-dialog',
  templateUrl: './role-delete-dialog.component.html',
  styleUrls: ['./role-delete-dialog.component.scss']
})
export class RoleDeleteDialogComponent implements OnInit {
  entity: IRole;

  @HostListener('window:keyup.esc')
  private onKeyUp() {
    this.dialogRef.close();
  }

  constructor(
    private dialogRef: MatDialogRef<RoleDeleteDialogComponent>,
    private service: RoleService,
    private eventManager: JhiEventManager,
  ) { }

  ngOnInit() {
  }

  confirmDelete(login) {
    this.service.delete(login).subscribe(() => {
      this.eventManager.broadcast({
        name: 'roleListModification',
        content: 'Deleted a role'
      });
      this.dialogRef.close(true);
    });
  }
}

@Component({
  selector: 'app-role-delete-popup',
  template: ''
})
export class RoleDeletePopupComponent implements OnInit, OnDestroy {
  private dialogRef: MatDialogRef<RoleDeleteDialogComponent>;

  constructor(private activatedRoute: ActivatedRoute, private router: Router, private dialog: MatDialog) {}

  ngOnInit() {
    this.activatedRoute.data.subscribe(({entity}: Data) => {
      setTimeout(() => {
        this.dialogRef = this.dialog.open(RoleDeleteDialogComponent, {
          disableClose: true,
          width: '500px',
        });

        this.dialogRef.componentInstance.entity = entity;

        this.dialogRef.afterClosed().subscribe(
          result => {
            this.router.navigate([{outlets: {popup: null}}], {replaceUrl: true, queryParamsHandling: 'merge'});
          },
          reason => {
            this.router.navigate([{outlets: {popup: null}}], {replaceUrl: true, queryParamsHandling: 'merge'});
          }
        );
      }, 0);
    });
  }

  ngOnDestroy() {
    this.dialogRef = null;
  }
}
