import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { RoleListSearch } from './role-list-search';
import { RoleService } from '../role.service';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { JhiEventManager } from 'ng-jhipster';
import { HttpResponse } from '@angular/common/http';
import { MatTableDataSource, PageEvent } from '@angular/material';

@Component({
  selector: 'app-role-list',
  templateUrl: './role-list.component.html',
  styleUrls: ['./role-list.component.scss']
})
export class RoleListComponent implements OnInit, OnDestroy {
  dataSource;
  displayedColumns: string[] = ['id', 'label', 'description', 'action'];
  searchService = new RoleListSearch();
  eventSubscriber: Subscription;
  constructor(
    private service: RoleService,
    private activatedRoute: ActivatedRoute,
    private titleService: Title,
    private eventManager: JhiEventManager,
  ) {
    this.dataSource = null;
  }

  ngOnInit() {
    this.titleService.setTitle('Role list');
    this.onRoleListModification();
    this.search();
  }

  ngOnDestroy() {
    this.eventManager.destroy(this.eventSubscriber);
  }

  onRoleListModification() {
    this.eventSubscriber = this.eventManager.subscribe('roleListModification', () => this.search());
  }

  search() {
    this.getList();
  }

  getList() {
    const req = this.searchService.getParams();
    this.service.query(req).subscribe((response: HttpResponse<any>) => {
      const result = response.body;
      if (result) {
        this.dataSource = new MatTableDataSource(result.data);
        this.searchService.setResponse(result);
      }
    });
  }

  onPageChange(event: PageEvent) {
    this.searchService.setPage(event.pageIndex + 1);
    this.searchService.setPageSize(event.pageSize);
    this.search();
  }
}
