import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Resolve, Routes } from '@angular/router';
import { catchError, map } from 'rxjs/operators';
import { IAccount } from '../../core';
import { AccountService } from '../../core';
import { PersonalSettingComponent } from './personal-setting/personal-setting.component';
import { EMPTY, of } from 'rxjs';
import { environment } from '../../../environments/environment';

@Injectable({providedIn: 'root'})
export class PersonalSettingResolve implements Resolve<IAccount> {
  constructor(private service: AccountService) {}

  resolve() {
    if (environment.design) {
      return of({});
    }
    return this.service.get().pipe(
      map((account: HttpResponse<IAccount>) => account.body),
      catchError(err => {
        return EMPTY;
      })
    );
  }
}

export const accountRoutes: Routes = [
  {
    path: 'profile',
    component: PersonalSettingComponent,
    resolve: {
      account: PersonalSettingResolve,
    },
    data: {
      authorities: ['can_change_profile', 'can_change_password'],
      pageTitle: 'Ci t ti khon'
    },
  }
];
