import { Injectable } from '@angular/core';
import { IAddress } from './address.model';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpClient, HttpResponse } from '@angular/common/http';

interface Page {
  data: IAddress[];
}

type EntityType = IAddress;
type EntityResponseType = HttpResponse<EntityType>;
type EntityListResponseType = HttpResponse<EntityType[]>;
type EntityPageResponseType = HttpResponse<Page>;

@Injectable({
  providedIn: 'root'
})
export class AddressService {
  private resourceUrl = '/api/addresses';
  constructor(private http: HttpClient) { }

  create(entity: EntityType): Observable<EntityResponseType> {
    const copy = this.convertEntityFromClient(entity);
    return this.http.post<EntityType>(this.resourceUrl, copy, {observe: 'response'})
      .pipe(map((res: EntityResponseType) => this.convertEntityFromServer(res)));
  }

  update(entity: EntityType): Observable<EntityResponseType> {
    const copy = this.convertEntityFromClient(entity);
    return this.http.put<EntityType>(this.resourceUrl, copy, {observe: 'response'})
      .pipe(map((res: EntityResponseType) => this.convertEntityFromServer(res)));
  }

  find(login: string): Observable<EntityResponseType> {
    return this.http.get<EntityType>(`${this.resourceUrl}/${login}`, {observe: 'response'})
      .pipe(map((res: EntityResponseType) => this.convertEntityFromServer(res)));
  }

  query(req?: any): Observable<EntityPageResponseType> {
    return this.http
      .get<Page>(this.resourceUrl, {params: req, observe: 'response'})
      .pipe(map((res: EntityPageResponseType) => this.convertPageServer(res)));
  }

  delete(login: string): Observable<EntityResponseType> {
    return this.http.delete(`${this.resourceUrl}/${login}`, {observe: 'response'});
  }

  list(req?: any): Observable<EntityListResponseType> {
    return this.http
      .get<EntityType[]>(`${this.resourceUrl}`, {params: req, observe: 'response'})
      .pipe(map((res: EntityListResponseType) => this.convertListServer(res)));
  }

  options(req?: any): Observable<EntityListResponseType> {
    return this.http
      .get<EntityType[]>(`${this.resourceUrl}/option`, {params: req, observe: 'response'})
      .pipe(map((res: EntityListResponseType) => this.convertListServer(res)));
  }

  private convertEntityFromClient(entity: EntityType): EntityType {
    const res: EntityType = Object.assign({}, entity, {
    });
    return res;
  }

  private convertEntityFromServer(res: EntityResponseType): EntityResponseType {
    return res;
  }

  private convertListServer(res: EntityListResponseType): EntityListResponseType {
    return res;
  }

  private convertPageServer(res: EntityPageResponseType): EntityPageResponseType {
    return res;
  }
}
